/**
 * 
 *
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 *
 */

let baseUrl = '';
let routerMode = 'hash';
let imgBaseUrl;


if (process.env.NODE_ENV == 'development') {
  imgBaseUrl = 'http://cangdu.org:8001/img/';
  baseUrl = 'http://www.zpdyht.com/hlylapi/v1';

} else if (process.env.NODE_ENV == 'production') {
  imgBaseUrl = 'http://cangdu.org:8001/img/';
  baseUrl = 'http://www.zpdyht.com/hlylapi/v1';
}

export {
  baseUrl,
  routerMode,
  imgBaseUrl,
}



