<template>
  <div class="toBeSetOut">
    <mOFheader title=""></mOFheader>
    <div class="logoBox">
      <img src="../assets/images/DDdetail/ddcf.png" alt="">
      <div class="banner">
        <p><img src="../assets/images/myOrderForm/ru.png" alt=""><span>{{main.product.product_name}}</span></p>
        <div class="label">
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
    <div class="user">
      <section>
        <p><i></i><span></span></p>
        <p>{{main.order_patient.patient_name}}</p>
      </section>
      <section>
        <p><i></i><span></span></p>
        <p>{{main.order_patient.patient_age}}</p>
      </section>
      <section>
        <p><i></i><span></span></p>
        <p>{{main.order_patient.patient_gender}}</p>
      </section>
      <section>
        <p><i></i><span></span></p>
        <p v-if="main.order_patient.is_hospital === 1"></p>
        <p v-else></p>
      </section>
    </div>
    <div class="address">
      <section>
        <span></span>
        <p>{{main.book_time_text}}</p>
      </section>
      <section>
        <span></span>
        <p>123 03</p>
      </section>
      <section>
        <span></span>
        <p></p>
      </section>
    </div>
    <div class="btn">
      <a href="#" class="btn2"><img src="../assets/images/DDdetail/tel.png" alt=""></a>
    </div>
  </div>
</template>

<script>
  import mOFheader from '../components/mOFheader.vue'
  export default {
    name: 'toBeSetOut',
    components: {
      mOFheader
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/toBeSetOut.scss';
</style>



