<template>
  <div class="orderHistory">
    <mOFheader title=""></mOFheader>
    <div class="navBox">
      <div class="nav">
        <span :class="fangshi === '' ? 'active' : ''" @click="change(0)"></span>
        <span :class="fangshi === '' ? 'active' : ''" @click="change(1)"></span>
      </div>
    </div>
    <div class="details">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import mOFheader from '../components/mOFheader.vue'
  export default {
    name: 'orderHistory',
    components: {
      mOFheader
    },
    data() {
      return {
        fangshi: '',
        date: '0807 - 0813'
      }
    },
    methods: {
      change: function(index) {
        var that = this;
        switch (index) {
          case 0:
            that.fangshi = ''
            that.date = '0807 - 0813'
            this.$router.push({
              name: 'orderHistoryWeek'
            })
            break;
          case 1:
            that.fangshi = ''
            that.date = '201708'
            this.$router.push({
              name: 'month'
            })
            break;
          default:

        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/orderHistory.scss';
</style>



